
.catalog-active > a {
  color: red !important;
}
.vue-scroll-response-test {
  height: 100%;
}
.vue-scroll-response-test > * {
    box-sizing: border-box;
}
.vue-scroll-response-test > #catalog {
    position: relative;
    height: 100%;
    overflow: auto;
    width: 200px;
    border: 1px solid red;
    position: absolute;
    left: 20px;
}
.vue-scroll-response-test > #content {
    position: relative;
    height: 100%;
    overflow: auto;
    margin-left: 240px;
    border: 1px solid red;
}
.vue-scroll-response-test > #content h1 a {
      display: block;
      color: #ccc;
      background: black;
      line-height: 100px;
}
.vue-scroll-response-test > #content h2 {
      display: block;
      height: 300px;
      background: #ccc;
      line-height: 2;
}



